import * as news from './action-type'

let defaultState = {
    list: [
        {
            id: 1,
            title: ',',
            content: 'P2P'
        },
        {
            id: 2,
            title: '',
            content: ''
        },
        {
            id: 3,
            title: '',
            content: ''
        },
        {
            id: 4,
            title: '',
            content: ' 81'
        },
        {
            id: 5,
            title: '',
            content: '82'
        }]
}

const newsReducer = (state = defaultState, action = {}) => {
    switch (action.type) {
        case news.GETNEWS:
            return state;
        case news.DELNEW:
            let temp = Object.assign([], state.list);
            temp.splice(action.index, 1);
            return {...state, list: temp}
        default:
            return state;
    }
}

export default newsReducer


